@charset "utf-8";
html,
body {
  background: #EFEFEF;
  margin: 0;
  padding: 0;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: 500;
}
/* 清除浮动 */
.clearfix:before,
.clearfix:after {
  content: "";
  display: table;
}
.clearfix:after {
  clear: both;
  overflow: hidden;
}
.clearfix {
  zoom: 1;
  /* for ie6 & ie7 */
}
.clear {
  clear: both;
  display: block;
  font-size: 0;
  height: 0;
  line-height: 0;
  overflow: hidden;
}
.fl {
  float: left;
}
.fr {
  float: right;
}
.oneline {
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  background: transparent;
}
::-webkit-scrollbar-corner {
  background: transparent;
}
::-webkit-resizer {
  background: transparent;
}
::-webkit-scrollbar-track,
::-webkit-scrollbar-thumb {
  border-radius: 999px;
  background-clip: content-box;
  border: solid 2px transparent;
}
/*滚动轨道*/
::-webkit-scrollbar-track {
  background-color: rgba(33, 37, 43, 0.26);
}
/*滚动条*/
::-webkit-scrollbar-thumb {
  min-height: 20px;
  background-color: rgba(33, 37, 43, 0.26);
}
::-webkit-scrollbar-thumb:hover {
  background-color: #5A6274;
  border-width: 1px;
}
::-ms-scrollbar {
  width: 8px;
  height: 8px;
  background: transparent;
}
::-ms-scrollbar-corner {
  background: transparent;
}
::-ms-resizer {
  background: transparent;
}
::-ms-scrollbar-track,
::-ms-scrollbar-thumb {
  border-radius: 999px;
  background-clip: content-box;
  border: solid 2px transparent;
}
/*滚动轨道*/
::-ms-scrollbar-track {
  background-color: rgba(33, 37, 43, 0.26);
}
/*滚动条*/
::-ms-scrollbar-thumb {
  min-height: 20px;
  background-color: rgba(33, 37, 43, 0.26);
}
::-ms-scrollbar-thumb:hover {
  background-color: #5A6274;
  border-width: 1px;
}
/* 主题背景色start */
.bg-black {
  background: #111 !important;
  color: #fff;
}
.bg-dark {
  color: #a6a8b1;
  background-color: #3a3f51 !important;
}
.bg-light {
  color: #58666e;
  background-color: #edf1f2 !important;
}
.bg-white {
  background: #fff !important;
}
.bg-white-only {
  background-color: #f9f9f9 !important;
}
.bg-light {
  background-color: #dde6e9 !important;
}
.bg-info {
  color: #fff;
  background-color: #1199c4 !important;
}
.bg-primary {
  color: #f4f3f9;
  background-color: #7266ba !important;
}
.bg-success {
  color: #c6efd0;
  background-color: #27c24c !important;
}
.bg-danger {
  color: #fff;
  background-color: #f05050 !important;
}
.bg-light .ant-menu-dark .ant-menu-item,
.bg-light .ant-menu-dark .ant-menu-item-group-title,
.bg-light .ant-menu-dark .ant-menu-item > a {
  color: #58666e;
}
.bg-light .ant-menu-dark .ant-menu-submenu-title .ant-menu-submenu-arrow::after,
.bg-light .ant-menu-dark .ant-menu-sub .ant-menu-submenu-title .ant-menu-submenu-arrow::after,
.bg-light .ant-menu-dark .ant-menu-submenu-title .ant-menu-submenu-arrow::before,
.bg-light .ant-menu-dark .ant-menu-sub .ant-menu-submenu-title .ant-menu-submenu-arrow::before {
  background: #58666e;
}
.bg-light .ant-menu-dark .ant-menu-inline.ant-menu-sub {
  background: #dde6e9;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.07) inset;
}
.bg-light .ant-menu-dark .ant-menu-submenu-open {
  color: #58666a;
}
.ant-menu-dark .ant-menu-submenu-title:hover {
  color: #fff;
  background-color: transparent;
}
/* 主题背景色end */
/* 主题设置样式start */
.settings.active {
  right: -1px;
}
.settings {
  position: fixed;
  top: 165px;
  right: -240px;
  z-index: 1010;
  width: 240px;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.settings .panel-body {
  padding: 5px 15px;
  background: #fff;
  border: 1px solid #dee5e7;
  border-top: 0;
}
.settings .panel-body .panel-item {
  padding: 10px 0;
  width: 100%;
  line-height: 24px;
}
.settings .setIcon {
  position: absolute;
  left: -45px;
  width: 45px;
  height: 45px;
  line-height: 45px;
  background: #f8f8f8;
  border: 1px solid #dee5e7;
  border-right: 0;
  border-radius: 2px 0 0 2px;
  cursor: pointer;
  text-align: center;
}
.settings .setIcon .setting {
  font-size: 14px;
  font-weight: bold;
  -webkit-animation: spin 1.5s infinite linear;
          animation: spin 1.5s infinite linear;
}
@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}
@keyframes spin {
  0% {
    -webkit-transform: rotate(0);
    transform: rotate(0);
  }
  100% {
    -webkit-transform: rotate(359deg);
    transform: rotate(359deg);
  }
}
.settings .panel-heading {
  width: 100%;
  background: #f8f8f8;
  border: 1px solid #dee5e7;
  border-right: 0;
  border-radius: 0 2px 2px 0;
  height: 45px;
  line-height: 42px;
  border-left: 0;
  padding: 0 15px;
  font-size: 14px;
  position: relative;
  border-bottom: 0;
}
.settings .panel-heading .rest_btn {
  position: absolute;
  right: 15px;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.settings .panel-heading .ant-btn-round.ant-btn-sm {
  font-size: 12px;
}
.settings .bgBox {
  background: #fdfdfd;
  width: 100%;
  padding: 15px;
  border-left: 1px solid #dee5e7;
}
.settings .bgBox .bgItem {
  float: left;
  position: relative;
  width: calc(50% - 5px);
  height: 45px;
  cursor: pointer;
}
.settings .bgBox .bgItem:nth-child(2n) {
  margin-left: 10px;
}
.settings .bgBox .bgItem > div {
  position: absolute;
  width: 50%;
  left: 0;
  top: 0;
}
.settings .bgBox .bgItem .bgLogo {
  height: 10px;
}
.settings .bgBox .bgItem .bgHead {
  left: 50%;
  height: 10px;
}
.settings .bgBox .bgItem .bgSlider {
  top: 10px;
  height: 20px;
}
.settings .bgBox .bgItem .bgMain {
  left: 50%;
  top: 10px;
  height: 20px;
  background: #f0f2f5;
}
.settings .bgBox .bgItem .bgMask {
  width: 100%;
  height: 30px;
  background: rgba(0, 0, 0, 0.5);
  text-align: center;
  line-height: 30px;
  color: #fff;
  font-size: 20px;
  font-weight: bold;
}
/* 主题设置样式end */
/* 盒子模型下的样式start */
.app_w1300 {
  width: 1300px;
  margin: 0 auto;
  height: 100%;
}
.app_w1300 .ant-layout-header.fixedHead,
.app_w1300 .ant-layout-header.fixedHead.fixedCol {
  width: 1100px;
  left: auto;
}
.app_w1300 .ant-layout-header.fixedHead.fixedCol {
  width: 1220px;
}
.app_w1300 .Main-wrap .left_slider {
  left: auto;
}
/* 盒子模型下的样式end */
/* 整体布局样式start */
.ant-layout-header .header {
  width: 100%;
  line-height: 64px;
}
.ant-layout-header.fixedHead {
  position: fixed !important;
  top: 0;
  left: 200px;
  width: calc(100% - 200px);
  z-index: 999;
}
.ant-layout-header.fixedHead.fixedCol {
  left: 80px;
  width: calc(100% - 80px);
}
.ant-layout-header.fixedHead + .main_wrap {
  margin: 80px 15px 15px 15px;
}
.Main-wrap .left_slider {
  height: 100%;
  position: fixed;
  z-index: 9;
  overflow-y: auto;
  overflow-x: hidden;
  left: 0;
  top: 0;
}
.Main-wrap .main.pl80 {
  padding-left: 80px;
}
.Main-wrap .main.pl200 {
  padding-left: 200px;
}
.Main-wrap .main_wrap {
  background: #fff;
  margin: 15px;
  padding: 15px;
}
.header .trigger,
.header .poweroff {
  font-size: 18px;
  padding: 0 24px;
  cursor: pointer;
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.ant-layout-header {
  padding: 0 !important;
  position: relative;
}
/* 整体布局样式end */
/* 左侧导航用户头像和logo样式start */
.left_slider .logo {
  width: 100%;
  height: 64px;
  background: #fff;
  text-align: center;
  position: relative;
}
.left_slider .logo img {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 60%;
  height: auto;
}
.left_slider .userBox {
  width: 100%;
  text-align: center;
  padding: 15px;
  margin-bottom: 10px;
  box-shadow: inset 0px 15px 15px -15px #000, inset 0px -15px 15px -15px #000;
}
.left_slider .userBox img {
  width: 80%;
  min-width: 50px;
  max-width: 96px;
  border-radius: 50%;
}
.left_slider .userBox p {
  font-size: 14px;
  line-height: 24px;
  margin-top: 15px;
  font-weight: bold;
}
.left_slider .userBox .userName {
  color: #f05050;
}
/* 左侧导航用户头像样式start */
